<script setup>
import { ref, onMounted } from 'vue'
import bpmGUI from './components/bpmGUI.vue'
import bpmCard from './components/bpmCard.vue'
import bpmGet from './components/bpmGet.vue'

const bpmList = ref([])
const isLoading = ref(true)
const errorMsg = ref('')

const parseBpmData = (rawData) => {
  if (!Array.isArray(rawData)) return []
  
  return rawData.map(item => {
    const [mainBpm, subBpmObj] = item
    const subBpmList = Object.entries(subBpmObj).map(([subBpm, contentList]) => ({
      subBpm,
      contentList
    }))
    
    return {
      mainBpm: Number(mainBpm),
      subBpmList
    }
  })
}

onMounted(async () => {
  try {
    const rawBpmData = await bpmGet.methods.getBPM()
    bpmList.value = parseBpmData(rawBpmData)
    errorMsg.value = ''
  } catch (error) {
    console.error('BPM表加载失败：', error)
    errorMsg.value = 'BPM表加载失败，请刷新重试'
  } finally {
    isLoading.value = false
  }
})
</script>

<template>
  <div class="table-container">
    <div v-if="isLoading" class="empty-tip">
      正在加载BPM表
    </div>
    
    <div v-else-if="errorMsg" class="empty-tip error">
      {{ errorMsg }}
    </div>
    
    <div v-else-if="bpmList.length > 0" class="bpm-list">
      <bpmGUI 
        :card-list="bpmList"
        :columns="5"
        :gap="20"
      >
        <template #default="{ cardData }">
          <bpmCard 
            :chart-info="cardData" 
          />
        </template>
      </bpmGUI>
    </div>
    <div v-else class="empty-tip">
      暂无BPM表数据
    </div>
  </div>
</template>

<style scoped>
.table-container {
  width: 100%;
  padding: 16px;
  box-sizing: border-box;
}

.bpm-list {
  margin-top: 20px;
}

.empty-tip {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 40px 0;
  color: var(--color-text-sub);
  font-size: 14px;
}

.empty-tip.error {
  color: var(--color-text-error); 
}
</style>