<template>
  <div class="card-container" :style="containerStyle">
    <div v-for="(mainGroup, mainIndex) in sortedCardList" :key="mainIndex" class="card-group">
      <div class="group-title">
        {{ Number(mainGroup.mainBpm) }}
      </div>
      <div v-for="(subItem, subIndex) in mainGroup.subBpmList" :key="subIndex" class="sub-bpm-row">
        <div class="content-wrapper">
          <div class="sub-bpm-label">
            {{ subItem.subBpm }}
          </div>
          <div class="card-grid" :style="getGridStyle()">
            <slot 
              v-for="(item, idx) in subItem.contentList" 
              :key="idx"
              :cardData="item"
              :index="idx"
              :mainBpm="mainGroup.mainBpm"
              :subBpm="subItem.subBpm"
            ></slot>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { computed } from 'vue';

const props = defineProps({
  cardList: {
    type: Array,
    required: true
  },
  columns: {
    type: Number,
    required: true
  },
  gap: {
    type: Number,
    default: 16
  },
  gridWidth: {
    type: [Number, String],
    default: 1500
  }
});

const containerStyle = computed(() => {
  return {
    width: typeof props.gridWidth === 'number' ? `${props.gridWidth}px` : props.gridWidth,
    margin: '0 auto',
    boxSizing: 'border-box'
  };
});

const getGridStyle = () => {
  return {
    '--column-count': props.columns,
    '--gap-size': `${props.gap}px`,
    '--column-width': `calc((100% - (var(--column-count) - 1) * var(--gap-size)) / var(--column-count))`
  };
};

const sortedCardList = computed(() => {
  const sortedGroups = [...props.cardList].sort((a, b) => {
    const numA = Number(a.mainBpm);
    const numB = Number(b.mainBpm);
    return numB - numA;
  });
  
  return sortedGroups.map(group => ({
    ...group,
    subBpmList: [...group.subBpmList].sort((a, b) => {
      const numA = Number(a.subBpm.replace('x', ''));
      const numB = Number(b.subBpm.replace('x', ''));
      return numA - numB;
    })
  }));
});
</script>

<style scoped>
.card-container {
  width: 100%;
  box-sizing: border-box;
}

.card-group {
  width: 100%;
  margin-bottom: 24px;
  box-sizing: border-box;
}

.group-title {
  height: 30px;
  line-height: 30px;
  background-color: var(--color-bg-sub);
  color: var(--color-text);
  padding: 0 16px;
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 8px;
  box-sizing: border-box;
  text-align: center;
}

.sub-bpm-row {
  width: 100%;
  margin-bottom: 16px;
  box-sizing: border-box;
}

.content-wrapper {
  display: flex;
  align-items: stretch;
  width: 100%;
  box-sizing: border-box;
  min-height: 80px;
}

.sub-bpm-label {
  width: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px 16px;
  background-color: var(--color-bg-sub);
  color: var(--color-text);
  font-size: 18px;
  font-weight: 50;
  box-sizing: border-box;
  word-break: break-word;
  flex-shrink: 0;
}

.card-grid {
  flex: 1;
  display: flex;
  flex-wrap: wrap;
  gap: var(--gap-size);
  box-sizing: border-box;
  padding: 8px;
  border-left: none;
  border-radius: 0 4px 4px 0;
}

.card-grid > * {
  width: var(--column-width) !important;
  flex-shrink: 0;
  box-sizing: border-box;
}
</style>