<template>
  <div class="card-grid" :style="gridStyle">
    <slot 
      v-for="(item, index) in cardList" 
      :key="index"
      :cardData="item"
      :index="index"
    ></slot>
  </div>
</template>

<script setup>
import { computed } from 'vue';

const props = defineProps({
  cardList: {
    type: Array,
    required: true
  },
  columns: {
    type: Number
  },
  gap: {
    type: Number
  },
  gridWidth: {
    type: [Number, String],
    default: 1500
  }
});

const gridStyle = computed(() => {
  return {
    width: typeof props.gridWidth === 'number' ? `${props.gridWidth}px` : props.gridWidth,
    '--column-count': props.columns,
    '--gap-size': `${props.gap}px`,
    '--column-width': `calc((100% - (var(--column-count) - 1) * var(--gap-size)) / var(--column-count))`
  };
});
</script>

<style scoped>
.card-grid {
  display: flex;
  flex-wrap: wrap;
  gap: var(--gap-size);
  box-sizing: border-box;
  margin: 0 auto;
}

.card-grid > * {
  width: var(--column-width) !important;
  flex-shrink: 0;
  box-sizing: border-box;
}
</style>