<script>
import axios from 'axios'

export default {
  name: 'infoGet',
  methods: {
    async getInfo(fieldMap = {}) {
      const net = '' 
      const net_files = `${net}/files`
      const jsonFile = `${net_files}/infos/infos.json`

      try {
        const response = await axios.get(jsonFile)
        const jsonData = response.data
        const header = {}
        Object.entries(fieldMap).forEach(([label, key]) => {
          header[label] = key
        })
        const dataRows = []
        for (const [levelId, levelInfo] of Object.entries(jsonData)) {
          if (!levelInfo.name || !levelInfo.display) continue;
          
          const row = {}
          Object.entries(fieldMap).forEach(([label, key]) => {
            const keyParts = key.split(':')
            let value = ''
            if (keyParts.length === 1) {
              value = levelInfo[keyParts[0]] ?? ''
            } else {
              const [category, prop] = keyParts
              value = levelInfo[category]?.[prop] ?? ''
            }
            row[label] = value
          })
          if (Object.keys(row).length > 0) {
            dataRows.push(row)
          }
        }
        return [header, ...dataRows]
        
      } catch (error) {
        console.error('获取数据失败：', error);
        console.error('实际请求的JSON文件路径：', jsonFile);
        const emptyHeader = {}
        Object.entries(fieldMap).forEach(([label, key]) => {
          emptyHeader[label] = key
        })
        return [emptyHeader]
      }
    }
  }
}
</script>