import { createRouter, createWebHashHistory } from 'vue-router'
import Layout from './Layout.vue'
import startPage from './startPage.vue'
import TableMain from './tableMain.vue'
import tableLevel from './tableLevel.vue'
import tableB30 from './tableB30.vue'
import tableBPM from './tableBPM.vue'
import tableInfo from './tableInfo.vue'

const routes = [
  {
    path: '/',
    component: Layout,
    children: [
      {
        path: '',
        name: 'start',
        component: startPage
      },
      {
        path: 'table',
        name: 'tableMain',
        component: TableMain
      },
      {
        path: 'table',
        children: [
          {
            path: 'level',    
            name: 'tableLevel',
            component: tableLevel
          },
          {
            path: 'b30',       
            name: 'tableB30',
            component: tableB30
          },
          {
            path: 'bpm',        
            name: 'tableBPM',
            component: tableBPM
          },
          {
            path: 'info',    
            name: 'tableInfo',
            component: tableInfo
          },
        ]
      }
    ]
  },
  {
    path: '/:pathMatch(.*)*',
    redirect: { name: 'start' }
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes,
  scrollBehavior() {
    return { top: 0 }
  }
})

export default router