  "HD物量": "HD:notecount",
  "HD-TAP": "HD:tap",
  "HD-DRAG": "HD:drag",
  "HD-HOLD": "HD:hold",
  "HD-FLICK": "HD:flick",
  "HD-LINE": "HD:line",

  "IN难度": "IN:dif",
  "IN定数": "IN:lvl",
  "IN谱师": "IN:tcharter",
  "IN谱师名义": "IN:charter",
