  "关联": "relation",
  "赛事": "competition",
  "预览开始": "previews",
  "预览结束": "previewe",
}

const fieldMap = {
  "曲名": "name",
  "曲师": "composer",
  "画师": "illustrator",
  "更新": "date",
  "章节": "chapter",
  "版本": "version",
  "BPM": "BPM",
  "时长": "duration",
  
  "EZ定数": "EZ:lvl",
  "EZ谱师名义": "EZ:tcharter",
  "EZ物量": "EZ:notecount",
  
  "HD定数": "HD:lvl",
  "HD谱师名义": "HD:charter",
  "HD物量": "HD:notecount",

  "IN定数": "IN:lvl",
  "IN谱师名义": "IN:charter",
  "IN物量": "IN:notecount",

  "AT定数": "AT:lvl",
  "AT谱师名义": "AT:charter",
  "AT物量": "AT:notecount",
}

onMounted(async () => {
  try {
    const infoData = await infoGet.methods.getInfo(fieldMap)
    infoList.value = infoData
    errorMsg.value = ''
  } catch (error) {
    console.error('信息表加载失败：', error)
    errorMsg.value = '信息表加载失败，请刷新重试'
    infoList.value = []
  } finally {
    isLoading.value = false
  }
})

</script>

<template>
  <div class="table-container">

    <div v-if="isLoading" class="empty-tip">
      正在加载信息表
    </div>
    
    <div v-else-if="errorMsg" class="empty-tip error">
      {{ errorMsg }}
    </div>
    
    <div v-else-if="infoList && infoList.length > 1" class="info-list">
      <infoGUI 
        :table-data="infoList"
        :table-width="1500"
        :cell-gap="10" 
        border-color="#d1d5db" 
      />
    </div>
    
    <div v-else class="empty-tip">
      暂无信息表数据
    </div>
  </div>
</template>

<style scoped>
.table-container {
  width: 100%;
  padding: 20px;
  box-sizing: border-box;
}

.info-list {
  margin-top: 20px;
}

.empty-tip {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 200px;
  color: var(--color-text-sub);
  font-size: 16px;
}

.empty-tip.error {
  color: var(--color-text-error);
}
</style>