<script setup>
import { ref, onMounted } from 'vue'
import levelGUI from './components/levelGUI.vue'
import levelCard from './components/levelCard.vue'
import levelGet from './components/levelGet.vue'

const levelList = ref(null)
const isLoading = ref(true)
const errorMsg = ref('')

onMounted(async () => {
  try {
    const levelData = await levelGet.methods.getLevel()
    levelList.value = levelData
    errorMsg.value = ''
  } catch (error) {
    console.error('定数表加载失败：', error)
    errorMsg.value = '定数表加载失败，请刷新重试'
  } finally {
    isLoading.value = false
  }
})
</script>

<template>
  <div class="table-container">
    <div v-if="isLoading" class="empty-tip">
      正在加载定数表
    </div>
    
    <div v-else-if="errorMsg" class="empty-tip error">
      {{ errorMsg }}
    </div>
    
    <div v-else-if="levelList" class="level-list">
      <levelGUI 
        :card-list="levelList"
        :columns="5"
        :gap="20"
      >
        <template #default="{ cardData }">
          <levelCard :chart-info="cardData" />
        </template>
      </levelGUI>
    </div>
    
    <div v-else class="empty-tip">
      暂无定数表数据
    </div>
  </div>
</template>

<style scoped>
  .level-list {
    margin-top: 20px;
  }

  .empty-tip {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 200px;
    color: var(--color-text-sub);
    font-size: 16px;
  }

  .empty-tip.error {
    color: var(--color-text-error);
  }
</style>