<script>
import axios from 'axios'

export default {
  name: 'b30Get',
  methods: {
    async getB30() {
      const net = '' 
      const net_files = `${net}/files`
      const jsonFile = `${net_files}/infos/infos.json`

      const difColor_map = {
        'EZ': '#0D9A28',
        'HD': '#0175B8',
        'IN': '#CF1313',
        'AT': '#383838'
      }

      const difficultyTypes = ['IN', 'AT']

      try {
        const response = await axios.get(jsonFile)
        const jsonData = response.data
        const dif = []
        for (const [levelId, levelInfo] of Object.entries(jsonData)) {
          if (!levelInfo.name) continue;
          if (!levelInfo.display) continue;
          for (const difType of difficultyTypes) {
            if (levelInfo[difType] && levelInfo[difType].lvl && levelInfo[difType].lvl.trim() !== '') {
              const levelValue = parseFloat(levelInfo[difType].lvl);
              const nameValue = levelInfo.name;
              if (!isNaN(levelValue)) {
                dif.push([
                  levelId,
                  levelValue,
                  difType,
                  nameValue
                ]);
              }
            }
          }
        }

        dif.sort((a, b) => b[1] - a[1]);

        for (const item of dif) {
          item[1] = Math.round(item[1] * 10) / 10;
          const originalDifType = item[2];
          item[2] = `${net}/assets/illLow/${item[0]}.png`;
          item.push(originalDifType);
          item.push(difColor_map[originalDifType] || '#383838');
        }

        const top3Copy = dif.slice(0, 3).map(item => [...item]);

        for (let i = 0; i < dif.length; i++) {
          const rank = i + 1;
          if (rank <= 27) {
            dif[i].push(`[B${rank}]`); 
            dif[i].push(false); 
          }
        }

        for (let i = 0; i < top3Copy.length; i++) {
          const rank = i + 1;
          top3Copy[i].push(`[P${rank}]`);
          top3Copy[i].push(true);
        }

        const finalDif = [...top3Copy, ...dif].slice(0, 30);;
        const b30Level = finalDif.map(item => item[1]);
        const rks = (b30Level.reduce((sum, val) => sum + val, 0) / 30);
        const nRks = rks.toFixed(2)
        const dRks = rks.toFixed(8);

        return [finalDif,[nRks,dRks]];
      } catch (error) {
        console.error('获取数据失败：', error);
        console.error('实际请求的JSON文件路径：', jsonFile);
        return {};
      }
    }
  }
}
</script>
