<template>
  <div class="table-container" :style="tableContainerStyle">
    <div v-if="tableTitle" class="table-title">{{ tableTitle }}</div>

    <div v-if="!hasValidData" class="empty-table-tip">
      {{ emptyTipText }}
    </div>

    <div
      v-else
      ref="tableWrapperRef"
      class="table-wrapper"
      @mousedown="handleMouseDown"
      @mouseleave="handleMouseLeave"
      @mouseup="handleMouseUp"
      @mousemove="handleMouseMove"
      :style="{ cursor: isDragging ? 'grabbing' : 'grab' }"
    >
      <table class="data-table">
        <thead>
          <tr>
            <th
              v-for="(value, key, index) in tableHeader"
              :key="`header-${index}`"
              class="table-th"
            >
              {{ key }}
            </th>
          </tr>
        </thead>

        <tbody>
          <tr
            v-for="(row, rowIndex) in tableBody"
            :key="`row-${rowIndex}`"
            :class="{ 'even-row': rowIndex % 2 === 0 }"
          >
            <td
              v-for="(cell, cellIndex) in Object.values(row)"
              :key="`cell-${cellIndex}`"
            >
              {{ cell }}
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script setup>
import { computed, ref, watch } from 'vue';

const props = defineProps({
  tableData: {
    type: Array,
    required: true
  },
  tableWidth: {
    type: [Number, String],
    default: '100%'
  },
  cellGap: {
    type: Number,
    default: 8
  },
  tableTitle: {
    type: String,
    default: ''
  }
});

const tableWrapperRef = ref(null);
const isDragging = ref(false);
const startX = ref(0);
const startScrollLeft = ref(0);

const hasValidData = computed(() => {
  if (!Array.isArray(props.tableData) || props.tableData.length === 0) {
    console.warn('tableData 为空数组');
    return false;
  }
  const header = props.tableData[0];
  if (typeof header !== 'object' || header === null || Array.isArray(header)) {
    console.warn('tableData 第0项不是有效字典（对象）');
    return false;
  }
  const body = props.tableData.slice(1);
  if (body.length === 0) {
    console.warn('tableData 无表体数据（仅表头）');
    return false;
  }
  const firstRow = body[0];
  if (typeof firstRow !== 'object' || firstRow === null || Array.isArray(firstRow)) {
    console.warn('tableData 表体数据不是有效字典（对象）');
    return false;
  }
  return true;
});

const tableHeader = computed(() => {
  return hasValidData.value ? props.tableData[0] : {};
});

const tableBody = computed(() => {
  return hasValidData.value ? props.tableData.slice(1) : [];
});

const emptyTipText = computed(() => {
  return '暂无表格数据';
});

const tableContainerStyle = computed(() => {
  return {
    width: typeof props.tableWidth === 'number' ? `${props.tableWidth}px` : props.tableWidth,
    '--cell-gap': `${props.cellGap}px`,
    margin: '0 auto'
  };
});

const handleMouseDown = (e) => {
  if (!tableWrapperRef.value) return;
  isDragging.value = true;
  startX.value = e.clientX;
  startScrollLeft.value = tableWrapperRef.value.scrollLeft;
  e.preventDefault();
};

const handleMouseMove = (e) => {
  if (!isDragging.value || !tableWrapperRef.value) return;
  const dx = e.clientX - startX.value;
  tableWrapperRef.value.scrollLeft = startScrollLeft.value - dx;
};

const handleMouseUp = () => {
  isDragging.value = false;
};

const handleMouseLeave = () => {
  isDragging.value = false;
};
</script>

<style scoped>
.table-container {
  box-sizing: border-box;
  min-height: 100px;
  margin: 0 auto;
}

.table-title {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 12px;
}

.table-wrapper {
  width: 100%;
  overflow-x: auto;
  overflow-y: hidden;
  -webkit-overflow-scrolling: touch;
  user-select: none;
}

.empty-table-tip {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 200px;
  color: var(--color-text-sub);
  font-size: 16px;
}

.data-table {
  width: auto;
  border-collapse: collapse;
  table-layout: auto;
  word-break: break-word;
  white-space: nowrap;
}

.data-table th {
  padding: var(--cell-gap);
  border: 1px solid var(--color-text-sub);
  background-color: var(--color-theme-sub);
  color: var(--color-text-opposite);
  font-weight: 600;
  text-align: center;
  vertical-align: middle;
  min-width: 80px;
  transition: background-color 0.2s ease;
}

.data-table td {
  padding: var(--cell-gap);
  border: 1px solid var(--color-text-sub);
  text-align: center;
  vertical-align: middle;
  min-width: 80px;
}

.even-row {
  background-color: var(--color-bg-sub);
}

.table-wrapper::-webkit-scrollbar {
  height: 6px;
}

.table-wrapper::-webkit-scrollbar-thumb {
  background-color: var(--color-bg-sub);
  border-radius: 3px;
}

.table-wrapper::-webkit-scrollbar-track {
  background-color: var(--color-bg-main);
}

.table-th {
  cursor: default;
  user-select: none;
  background-color: inherit;
  color: inherit;
  border: inherit;
}
</style>