<script>
import axios from 'axios'

export default {
  name: 'bpmGet',
  methods: {
    transformSongPaths(data, net) {
      if (Array.isArray(data)) {
        return data.map(item => this.transformSongPaths(item, net));
      }
      else if (typeof data === 'object' && data !== null) {
        const newObj = {};
        for (const key in data) {
          newObj[key] = this.transformSongPaths(data[key], net);
        }
        return newObj;
      }
      else if (typeof data === 'string') {
        return `${net}/assets/illLow/${data}.png`;
      }
      return data;
    },

    async getBPM() {
      const net = '' 
      const net_files = `${net}/files`
      const jsonFile = `${net_files}/infos/bpm.json`

      try {
        const response = await axios.get(jsonFile)
        const jsonData = response.data;
        
        const transformedData = this.transformSongPaths(jsonData, net);
        return transformedData;
      } catch (error) {
        console.error('获取数据失败：', error);
        console.error('实际请求的JSON文件路径：', jsonFile);
        return {};
      }
    }
  }
}
</script>