<script setup>
import { ref, onMounted } from 'vue'
import b30GUI from './components/b30GUI.vue'
import b30Card from './components/b30Card.vue'
import b30Get from './components/b30Get.vue'

const b30List = ref(null)
const isLoading = ref(true)
const errorMsg = ref('')
const rksL = ref([])

onMounted(async () => {
  try {
    const [b30Data,rksLData] = await b30Get.methods.getB30()
    b30List.value = b30Data
    rksL.value = rksLData
    errorMsg.value = ''
  } catch (error) {
    console.error('B30加载失败：', error)
    errorMsg.value = 'B30加载失败，请刷新重试'
  } finally {
    isLoading.value = false
  }
})
</script>

<template>
  <div class="table-container">
    <div v-if="isLoading" class="empty-tip">
      正在加载B30表
    </div>
    
    <div v-else-if="errorMsg" class="empty-tip error">
      {{ errorMsg }}
    </div>
    
    <div v-else-if="b30List" class="b30-list">
      <b30GUI 
        :card-list="b30List"
        :columns="3"
        :gap="20"
      >
        <template #default="{ cardData }">
          <b30Card :chart-info="cardData" />
        </template>
      </b30GUI>

      <div class="fixed-left-text" >
      RKS {{ rksL[0] }} ({{ rksL[1] }})
    </div>
    </div>
    
    <div v-else class="empty-tip">
      暂无B30表数据
    </div>
    
  </div>
</template>

<style scoped>
  .b30-list {
    margin-top: 20px;
  }

  .fixed-left-text {
  left: 50%;
  top: 10%;
  text-align: center;
  padding: 16px 10px;
  color: var(--color-text-sub);
  font-size: 20px;
  }
</style>